@font-face {
    font-family: OpenSans-Quark;
    src: url(font/quark/Quark-Light.otf);
}

@font-face {
    font-family: OpenSans-Quark;
    src: url(font/quark/Quark-Bold.otf);
    font-weight: bold;
}

@font-face {
    font-family: OpenSans-Quark;
    src: url(font/opensans/OpenSans-Light.ttf);
    unicode-range: U+41-5A, U+61-7A, U+C0-FF;
}

@font-face {
    font-family: OpenSans-Quark;
    src: url(font/opensans/OpenSans-Bold.ttf);
    font-weight: bold;
    unicode-range: U+41-5A, U+61-7A, U+C0-FF;
}

html {
    height: 100%;
}

* {
    font-family: OpenSans-Quark, Helvetica, sans-serif;
}

body {
    height: 100%;
    /* background-color: aqua; */
}

.nav-link {
    margin-right: 1rem;
    margin-left: 1rem;
}

.navActive {
    font-weight: bold;
    /* border-bottom: solid 3px #135193; */
}

.mobile-nav-container {
    background-color: #135193;
    width: 100%;
    height: 100%;
}

.mobile-nav {
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 50%;
    margin: 0;
    /* margin: -15% 0 0 -25%; */
}

.mobile-nav .nav-link {
    color: #FFFFFF75;
}

.mobile-nav .navActive {
    color: #FFFFFF;
    border-bottom: 0px;
}

.open-mobile-menu {
    text-decoration: none;
    color: #135193;
    padding: 2rem;
}

.dismiss-mobile-menu {
    text-decoration: none;
    color: white;
    padding: 2rem;
}

#root {
    height: 100%;
}

.article-box {
    background-color: #FFFFFF90;
    border: #135193 5px solid;
}

.article-box:hover {
    background-color: #13519330;
    transition: 0.5s;
}

.article-read-more {
    border-width: 1rem;
    margin: 0.5rem;
    text-align: right;
}

.article-read-more a {
    text-decoration: none;
    font-weight: bold;
    color: #00000075;
}

.article-read-more a:hover {
    text-decoration: none;
    font-weight: bold;
    color: black;
    border-bottom: #135193 2px solid;
}

