.wrapper {
    display: flex;
    align-items: stretch;
}

.hr {
    border-color: #135193;
    border-width: thick;
}

.footer {
    background-color: #135193;
    padding: 1em;
    /* padding-top: 2rem; */
    color: white;
    text-align: center;
    font-size: 0.75em;
}

.imgprofile {
    height: 200px;
}

.social-img {
    height: 2.5rem;
}

