.header-box {
    background-color: #135193;
    border-radius: 0.35rem;
    color: white;
    padding: 0.5rem;
    margin: 2.5rem auto 1rem auto;
}

.header-box h2, h3 {
    margin: auto;
}

.social-box {
    background-color: #0984e3;
    /*padding: 1rem;*/
    border-radius: 0.35rem;
}

.my-social-link {
    font-size: 1.25rem;
    font-weight: bold;
    text-decoration: none;
    color: black;
}

.my-social-link:hover {
    font-weight: bold;
    text-decoration: none;
    color: black;
    text-shadow: 2px 2px 4px #ccc;
    transition: 0.2s;
}

.social-svg {
    fill: black;
}

p {
    font-size: 1.25rem;
}

.head-text {
    border-bottom: 5px #135193 solid;
}

.pro-skill-box {
    /*background-color: #2ed573;*/
    border-radius: 0.5rem;
    padding: 0.5rem;
}

.pro-skill-box ul {
    padding: 1rem;
}

.pro-skill-box ul:hover {
    /*border: 10px black solid;*/
    border-radius: 0.5rem;
    box-shadow: 3px 3px 5px 6px #ccc;
    transition: 0.2s;
}

.pro-skill-box ul {
    list-style: none;
}

.pro-skill-icon {
    width: 2rem;
    margin-right: 1rem;
    margin-bottom: 0.75rem;
}

li {
    font-size: 1.2rem;
}

.interested-box {
    padding: 1rem;
    font-size: 1.25rem;
    font-weight: bold;
}

