.my-fields {
    background-color: #0984e3;
    border-radius: 0.35rem;
}

.page-name {
    margin: 1.5rem auto 1.5rem auto;
    width: 100%;
}

.my-circle-point {
    border-radius: 50%;
    background-color: #135193;
    height: 1rem;
    width: 1rem;
    display: inline-block;
}

.unigo-icon {
    width: 10rem;
    border-radius: 1rem;
}

.unigo-poster {
    width: 50%;
}

.fmh-logo {
    width: 10rem;
}

